import {
   Component, EventEmitter, Input,
  OnInit, Output,
} from '@angular/core';
@Component({
  selector: 'amexio-menu-ce',
  template: `
    <div 
    [ngClass]="{textLeftPosition: (position == 'left'),
     textRightPosition: (position == 'right'), 
     textCenterPosition: (position == 'center')}">
        <div *ngFor="let node of data" 
        [ngClass]='animatedClass'
       >
        <a style="text-decoration: none;" href="#"
        (mouseover)="node.hover=true" 
        (mouseleave)="node.hover=false"
        (click)="onMenuClick(node)"
        [ngStyle]="{'font-family':font, 'font-size': size+'px', 'color': fontColor}"
        >{{node.label}}</a>
      </div>
    </div>
  `,
})
export class AmexioMenuCEComponent implements OnInit {

  constructor() { }

  @Input() data: any;

  @Input('font') font = 'Titillium Web';

  @Input() size: string;

  @Input('font-color') fontColor: string;

  @Output() onClick: any = new EventEmitter<any>();

  @Input('animation-type') type: any;

  @Input('menu-positioning') position = 'left';

  @Input('menu-alignment') menuAlignment = 'left';
  animatedClass: any;

  ngOnInit() {
    this.data.forEach((element: any) => {
      element['hover'] = false;
    });

    if (this.type) {
      this.animatedClass = 'bmenu' + this.type;
    }
  }
  onMenuClick(event: any) {
    this.onClick.emit(event);
  }
}
